import React, { FunctionComponent, ReactElement, useMemo } from 'react';
import { animated, config, useSpring } from 'react-spring';

import styled from '@emotion/styled';

import { PERSPECTIVE, Length, CoordValue } from '../PERSPECTIVE';
import { CoordinateLike } from './Coordinate';

// The pythagoras distance (1/sqrt(2)) with a little tweaking for better looks
const PIXEL_FRIENDLY_PYTHAGORAS = 0.8;

export const AbsolutelyPositionedContainer = styled(animated.div)`
	position: absolute;
	left: 50%;
	top: 50%;
	width: 0;
	height: 0;
	border-radius: 50%;
	> * {
		position: absolute;
		top: 0;
		left: 0;
	}
`;

type ViewportComponentProps = {
	magnify?: number;
	center: CoordinateLike;
	overlay?: ReactElement;
};

export const Viewport: FunctionComponent<ViewportComponentProps> = ({
	overlay,
	center,
	magnify = 1,
	children,
	...rest
}) => {
	const [translateX, translateY] = useMemo(
		() =>
			PERSPECTIVE.toPixels(center.x, center.y, center.z).map(
				(n, i) => Math.round(-n) + (i ? 0 : 0.5)
			),
		[center]
	);

	const springStyles = useSpring({
		config: config.molasses,
		to: {
			transform: `translate(
				${translateX * magnify}px,
				${translateY * magnify}px
			)`
		}
	});

	return (
		<AbsolutelyPositionedContainer style={springStyles}>
			<ViewportSvgContainer magnify={magnify} children={children} />
			{overlay}
		</AbsolutelyPositionedContainer>
	);
};

export const ViewportSvgContainer: FunctionComponent<
	{ magnify?: number } & React.SVGProps<SVGSVGElement>
> = ({ magnify = 1, ...rest }) => (
	<svg
		width="1px"
		height="1px"
		overflow="visible"
		shapeRendering="geometricPrecision"
		viewBox={[0, 0, 1 / magnify, 1 / magnify].join(' ')}
		{...rest}
	/>
);

const NoAxis = styled.div`
	position: absolute;
	transform-origin: top left;
`;

const YAxis = styled(NoAxis)`
	transform: skewY(-${PERSPECTIVE.degrees}deg)
		scaleX(${PIXEL_FRIENDLY_PYTHAGORAS});
`;

const ZAxis = styled(NoAxis)`
	transform: rotate(-${PERSPECTIVE.degrees}deg)
		skewX(${PERSPECTIVE.degrees}deg)
		scaleX(${Math.sqrt(PIXEL_FRIENDLY_PYTHAGORAS)})
		scaleY(${PIXEL_FRIENDLY_PYTHAGORAS});
`;

const XAxis = styled(NoAxis)`
	transform: skewY(${PERSPECTIVE.degrees}deg)
		scaleX(${PIXEL_FRIENDLY_PYTHAGORAS});
`;

export const ViewportHtmlContainer: FunctionComponent<
	{
		axis?: 'x' | 'y' | 'z';
		width: Length;
		height: Length;
	} & Partial<CoordinateLike>
> = ({ x = 0, y = 0, z = 0, axis, width, height, children }) => {
	const [left, top] = PERSPECTIVE.toPixels(x, y, z).map(n => Math.round(n));
	const [pixelWidth] = PERSPECTIVE.toPixels(0, width, height).map(n =>
		Math.round(n)
	);
	const Axis =
		axis === 'y'
			? YAxis
			: axis === 'x'
			? XAxis
			: axis === 'z'
			? ZAxis
			: NoAxis;
	return (
		<Axis
			style={{
				left: Math.round(left),
				top: Math.round(top),
				width:
					Math.round(
						axis
							? pixelWidth / PIXEL_FRIENDLY_PYTHAGORAS
							: pixelWidth
					) + 'px',
				height:
					Math.round(
						axis
							? pixelWidth / PIXEL_FRIENDLY_PYTHAGORAS
							: pixelWidth
					) + 'px'
			}}
		>
			{children}
		</Axis>
	);
};
