import styled from '@emotion/styled';
import { FunctionComponent, useMemo, useState } from 'react';

import { Anchor, Coordinate, MonochromeBox, Viewport } from '../isometric';
import { ViewportHtmlContainer } from '../isometric/3d/Viewport';
import { distanceToCameraComparator } from '../isometric/PERSPECTIVE';
import { ActiveEntityOverlay } from '../ui/ActiveEntityOverlay';
import { ContextMenu, ContextMenuButton } from '../ui/ContextMenu';
import { RandomScene } from './Scene';

function getCenterTopCoordinateOfBox(coord: Coordinate) {
	return coord.clone().transform(0.5, 0.5, 1);
}

const HelloWorldContextMenu: FunctionComponent<{
	coordinate?: Coordinate;
	close: () => void;
}> = ({ coordinate, close }) => {
	if (!coordinate) {
		return null;
	}
	return (
		<ViewportHtmlContainer {...coordinate} width={0} height={0}>
			<ContextMenu>
				<ContextMenuButton
					onClick={() => {
						close();
						window.alert(JSON.stringify(coordinate, null, '    '));
					}}
				>
					Show coordinates
				</ContextMenuButton>
			</ContextMenu>
		</ViewportHtmlContainer>
	);
};

const HelloWorldSceneTerrain: FunctionComponent<
	{
		coordinate: Coordinate;
	} & (typeof MonochromeBox extends FunctionComponent<infer T> ? T : {})
> = ({ coordinate: coord, ...monochromeBoxProps }) => {
	return (
		<Anchor key={coord.toString()} x={coord.x} y={coord.y} z={coord.z}>
			<MonochromeBox {...monochromeBoxProps} />
		</Anchor>
	);
};

export const HelloWorldScene: FunctionComponent = () => {
	const scene = useMemo(() => new RandomScene(25, 25), []);

	const [centerCoord, setCenterCoord] = useState(
		getCenterTopCoordinateOfBox(new Coordinate(12, 12, 0.5))
	);

	const [contextMenuCoord, setContextMenuCoord] = useState<
		Coordinate | undefined
	>(undefined);

	const terrain = useMemo(
		() =>
			scene.terrain
				.slice()
				.sort(distanceToCameraComparator)
				.map(coord => (
					<HelloWorldSceneTerrain
						key={coord.toString()}
						coordinate={coord}
						onClick={event => {
							event.preventDefault();
							setContextMenuCoord(undefined);
							setCenterCoord(getCenterTopCoordinateOfBox(coord));
						}}
						onContextMenu={event => {
							event.preventDefault();
							setContextMenuCoord(
								getCenterTopCoordinateOfBox(coord)
							);
						}}
					/>
				)),
		[scene.terrain]
	);

	return (
		<>
			<Viewport
				center={centerCoord}
				magnify={1}
				overlay={
					<HelloWorldContextMenu
						coordinate={contextMenuCoord}
						close={() => setContextMenuCoord(undefined)}
					/>
				}
			>
				{terrain}
			</Viewport>
			<ActiveEntityOverlay />
		</>
	);
};
