import { Coordinate } from '../isometric';

export class Scene {
	public terrain: Coordinate[] = [];
}

export class RandomScene extends Scene {
	constructor(width: number, height: number) {
		super();
		this.terrain = Array.from(new Array(width * height)).map(
			(_, i) =>
				new Coordinate(
					i % width,
					Math.floor(i / width),
					Math.random() * 0.3
				)
		);
	}
}
