import React, { FunctionComponent, useMemo } from 'react';

import { CoordArray, CoordObject, Length, PERSPECTIVE } from '../PERSPECTIVE';
import { LinePath } from '../objects/LinePath';
import { CoordinateLike } from './Coordinate';

const Crosshair: FunctionComponent<{ size: Length }> = ({ size = 4 }) => {
	const lines: CoordArray[][] = [
		[
			// over the x axis
			[-1 * size, 0, 0],
			[size, 0, 0]
		],
		[
			// over the y axis
			[0, -1 * size, 0],
			[0, size, 0]
		],
		[
			// over the z axis
			[0, 0, -1 * size],
			[0, 0, size]
		]
	];

	return (
		<>
			{lines.map((points, i) => (
				<LinePath
					key={i}
					path={points}
					stroke={'rgba(0,0,0)'}
					strokeWidth={0.5}
				/>
			))}
		</>
	);
};

export const Anchor: FunctionComponent<
	{
		crosshairSize?: Length;
	} & CoordinateLike
> = ({ x = 0, y = 0, z = 0, children, crosshairSize = 0 }) => {
	const pixels = useMemo(() => PERSPECTIVE.toPixels(x, y, z), [x, y, z]);

	return (
		<svg x={pixels[0]} y={pixels[1]} overflow={'visible'}>
			{crosshairSize ? <Crosshair size={crosshairSize} /> : null}
			{children}
		</svg>
	);
};
